body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

h1 {
  margin-bottom: 20px;
  font-size: 24px;
  font-weight: bold;
}

#breed-select {
  width: 50%;
  color: #333;
  background-color: #f2f2f2;
  border: 1px solid #ccc;
  border-radius: 5px;
  justify-content: center;
  margin-bottom: 10px;
  padding: 10px;
  font-size: 16px;
}

.loader {
  text-align: center;
  color: #333;
  font-size: 16px;
  font-weight: bold;
}

.error {
  text-align: center;
  color: red;
  font-size: 16px;
}

#cat-info {
  text-align: center;
  margin-top: 20px;
}

#cat-image {
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: 50%;
  margin-bottom: 10px;
}

#cat-breed {
  margin-bottom: 5px;
  font-size: 18px;
  font-weight: bold;
}

